.game-container {
  position: relative;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
}

.menu__container {
  display: grid;
  width: 90vw;
  max-width: 750px;
  min-width: 310px;
  margin: 0 auto;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
}

.game__button {
  width: 110px;
  padding: 10px 20px;
  font-size: 1.5rem;
  font-size: 600;
  border-radius: 40px;
  color: var(--color-light);
  text-align: center;
  letter-spacing: 1px;
  cursor: pointer;
}

.menu__button {
  background-color: var(--color-bg-variant);
  transition: all 0.3s;
}

.menu__button:hover {
  background-color: var(--color-pink);
}

.restart__button {
  background-color: var(--color-bg-variant);
  transition: all 0.3s;
}

.restart__button:hover {
  background-color: var(--color-pink);
}

.menu__container .main__logo {
  width: 60px;
  height: 60px;
  margin: 0 auto;
  padding: 2px;
}

.game__container {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 76px 0 136px 0;
}

.player__card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 140px;
  height: 165px;
  margin: 45px;
  border-radius: 20px;
  border: 3px solid;
  border-bottom: 15px solid;
  background-color: var(--color-light);
}

.player__card img {
  position: absolute;
  width: 50px;
  left: calc(50% - 25px);
  top: -25px;
}

.player__card h2 {
  margin-top: 32px;
  font-size: 2rem;
  text-align: center;
}

.player__card p {
  font-size: 5.5rem;
  font-weight: 600;
  text-align: center;
}

.game__board {
  position: relative;
}

.white-board {
  width: 632px;
  height: 584px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
}

.black-board {
  position: absolute;
  width: 632px;
  height: 594px;
  left: 0;
  top: 0.4px;
  z-index: -1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
}

.winner-color {
  position: absolute;
  width: 100%;
  height: calc(50% - 245px);
  bottom: 0;
  z-index: -2;
  border-radius: 60px 60px 0 0;
  background-color: var(--color-bg-variant);
}

.winner-pink {
  background-color: var(--color-pink);
}

.winner-yellow {
  background-color: var(--color-yellow);
}

@media (max-width: 1060px) {
  .menu__container {
    margin-top: 10px;
  }

  .game__container {
    flex-wrap: wrap;
    margin-top: 15px;
  }

  .player__card {
    order: 1;
    width: 280px;
    height: 100px;
    margin: 20px;
    border-bottom: 12px solid;
    flex-direction: row;
    justify-content: space-evenly;
  }

  .player__card p {
    align-self: center;
  }

  .player-1-card img {
    left: -25px;
    top: calc(50% - 30px);
  }

  .player-2-card img {
    left: auto;
    right: -25px;
    top: calc(50% - 30px);
  }

  .player-2-card h2 {
    order: 2;
  }

  .player-2-card p {
    order: 1;
  }

  .game__board {
    order: 2;
  }

  .winner-color {
    height: calc(50% - 290px);
  }
}

@media (max-width: 640px) {
  .game__container {
    margin-bottom: 180px;
  }

  .player__card {
    width: 38%;
    height: 12vw;
    min-height: 100px;
    margin: 10px;
    flex-direction: column;
  }

  .player__card h2 {
    order: 1;
    font-size: 1.5rem;
    margin-top: 10px;
  }

  .player__card p{
    order: 2;
    font-size: 4.5rem;
  }

  .white-board{
    width: 95vw;
    min-width: 304px;
    height: 88.3vw;
    min-height: 282.56px;
  }

  .black-board {
    width: 95vw;
    min-width: 304px;
    height: 89.6vw;
    min-height: 286.72px;
  }

  .winner-color {
    height: calc(50% - 40vw);
  }
}




