.winner__container {
  position: absolute;
  width: 290px;
  height: 180px;
  bottom: calc(50%);
  left: calc(50% - 145px);
  color: var(--color-dark);
}

.winner {
  width: 100%;
  height: 100%;
  position: relative;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
}

.winner button:hover {
  background-color: var(--color-yellow);
  cursor: pointer;
}

.winner h3 {
  position: absolute;
  width: 100%;
  top: 15%;
  left: 0;
  text-align: center;
}

.winner p {
  position: absolute;
  width: 100%;
  top: 25%;
  left: 0;
  font-size: 5.5rem;
  font-weight: 600;
  text-align: center;
}

.winner button {
  width: 135px;
  position: absolute;
  top: 60%;
  left: calc(50% - 67px);
  background-color: var(--color-bg-variant);
}

@media (max-width: 1060px) { 
  .winner__container {
    bottom: calc(50% - 498px);
  }
}

@media (max-width: 640px) {
  .winner__container {
    bottom: -155vh
  }
}